.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom-width: 2px;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

@media (min-width: 768px) {
  .navbar {
    padding-top: 0px;
    padding-bottom: 0px;
  }
}

.navbar-nav {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: -100%;
  display: flex;
  height: 100%;
  width: 15rem;
  flex-direction: column;
  --tw-bg-opacity: 1;
  background-color: rgb(191 219 254 / var(--tw-bg-opacity));
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  padding-left: 2rem;
}

@media (min-width: 768px) {
  .navbar-nav {
    position: relative;
    width: auto;
    flex-direction: row;
    background-color: transparent;
  }
}

.logo {
  margin-right: 1.25rem;
}

@media (min-width: 768px) {
  .logo {
    margin-right: 0.5rem;
  }
}

@media (min-width: 1024px) {
  .logo {
    margin-right: 1.25rem;
  }
}

.nav-item {
  padding-left: 0px;
  padding-right: 0px;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
}

@media (min-width: 768px) {
  .nav-item {
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    padding-top: 0px;
    padding-bottom: 0px;
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 400;
  }
}

.nav-link {
  padding-bottom: 46px;
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
  transition-duration: 150ms;
}

.nav-link:hover {
  border-bottom-width: 4px;
  border-color: transparent;
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
}

@media (min-width: 768px) {
  .nav-link {
    --tw-text-opacity: 1;
    color: rgb(107 114 128 / var(--tw-text-opacity));
  }

  .nav-link:hover {
    --tw-border-opacity: 1;
    border-color: rgb(255 125 27 / var(--tw-border-opacity));
  }
}

