.aside-nav {
  display: flex;
  align-items: center;
}

.user {
  margin-left: 2rem;
  width: 3rem;
  cursor: pointer;
  border-radius: 9999px;
  border-width: 2px;
  transition-duration: 100ms;
}

.user:hover {
  --tw-border-opacity: 1;
  border-color: rgb(255 172 106 / var(--tw-border-opacity));
}

/*  Sneackers Info */

.price-content {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
}

@media (min-width: 768px) {
  .price-content {
    flex-direction: column;
    align-items: flex-start;
  }
}

.incrementation-btn {
  cursor: pointer;
  font-size: 1.875rem;
  line-height: 2.25rem;
  --tw-text-opacity: 1;
  color: rgb(255 125 27 / var(--tw-text-opacity));
}

.add-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 125 27 / var(--tw-bg-opacity));
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  transition-duration: 200ms;
}

.add-btn:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(255 172 106 / var(--tw-bg-opacity));
}

.checkout-btn {
  margin-top: 1.5rem;
  width: 100%;
  border-radius: 0.5rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 125 27 / var(--tw-bg-opacity));
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
  line-height: 1.5rem;
